import React, { Component } from "react";
import { Link } from "react-router-dom";
class Navbar extends Component {
  render() {
    return (
      <nav className="navbar navbar-expand-sm bg-danger navbar-dark py-2">
        <Link
          className="navbar-brand"
          to="/home"
          style={{ color: "black", fontWeight: "bold" }}
        >
          Welcome
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#nav-main"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="nav-main">
          <ul className="navbar-nav mr-auto">
            <li className="nav-item">
              <Link className="nav-link" to="/planets/viewall">
                All Planets
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/planets/favorites">
                Favorites
              </Link>
            </li>
          </ul>
        </div>
      </nav>
    );
  }
}

export default Navbar;
