import React, { Component } from "react";
import config from "../config.json";
import http from "../service/httpService";
import Favorites from "./favorites";
class Planets extends Component {
  state = {
    planets: [],
  };
  async componentDidMount() {
    const { data: planets } = await http.get(config.url);
    this.setState({ planets });
  }

  handleFav = (planet) => {
    let { planets } = this.state;
    planets = planets.map((p) => {
      if (p.id === planet.id) p.isFavourite = true;
      return p;
    });
    this.setState({ planets });
  };
  handleRemove = (planet) => {
    let { planets } = this.state;
    planets = planets.map((p) => {
      if (p.id === planet.id) p.isFavourite = false;
      return p;
    });
    this.setState({ planets });
  };
  render() {
    const { planets } = this.state;

    const { selectedTab } = this.props.match.params;

    return (
      <div className="container text-center">
        {selectedTab === "viewall" && (
          <table className="table">
            <thead>
              <tr>
                <th>Planet Name</th>
                <th> Add to Favorites</th>
              </tr>
            </thead>
            <tbody>
              {planets.map((planet) => (
                <tr key={planet.id}>
                  <td>{planet.name}</td>
                  <td>
                    <button
                      className="btn btn-info btn-sm"
                      onClick={() => this.handleFav(planet)}
                      disabled={planet.isFavourite}
                    >
                      Add
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
        {selectedTab === "favorites" && (
          <Favorites onRemove={this.handleRemove} favorites={planets} />
        )}
      </div>
    );
  }
}

export default Planets;
