import React, { Component } from "react";
import Planets from "./planets";
class Favorites extends Component {
  render() {
    let favorites = this.props.favorites.filter((p) => p.isFavourite === true);
    return (
      <div className="container text-center">
        {favorites.length === 0 ? (
          <h4 className="pt-2">
            There is no Favorite planet added, please select one...
          </h4>
        ) : (
          <table className="table">
            <thead>
              <tr>
                <th>Planet Name</th>
                <th> Remove from Favorites</th>
              </tr>
            </thead>
            <tbody>
              {favorites.map((planet) => (
                <tr key={planet.id}>
                  <td>{planet.name}</td>
                  <td>
                    <button
                      className="btn btn-info btn-sm"
                      onClick={() => this.props.onRemove(planet)}
                    >
                      Remove
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>
    );
  }
}

export default Favorites;
